@include font(proxima-nova, proxima-nova);
@include font(proxima-nova-semibold, proxima-nova-semibold);
@include font(proxima-nova-bold, proxima-nova-bold);
@include font(proxima-nova-regular-italic, proxima-nova-regular-italic);
@include font(intro-bold, intro-bold);

@mixin ib {
  font-family: 'intro-bold', sans-serif; }

@mixin proxima {
  font-family: 'proxima-nova', sans-serif; }

@mixin proxima-sb {
  font-family: 'proxima-nova-semibold', sans-serif; }

@mixin proxima-b {
  font-family: 'proxima-nova-bold', sans-serif; }

@mixin proxima-it {
  font-family: 'proxima-nova-regular-italic', sans-serif; }


body {
  @include proxima;
  color: $dark-brown;
  line-height: 1.3; }


.title-h1,
.title-h2,
.title-h3,
.title-h4,
.title-h5 {
  @include ib;
  color: $black;
  &--white {
    color: #fff; } }


.title-h1 {
  font-size: rem(40);
  line-height: 1.43;
  @include r(1024) {
    font-size: rem(34);
    line-height: 1.18; }
  @include r(767) {
    font-size: remMob(26); } }

.title-h2 {
  font-size: rem(35);
  @include r(1024) {
    font-size: rem(30); }
  @include r(767) {
    font-size: remMob(23); } }

.title-h3 {
  font-size: rem(30);
  @include css-lock(1.222, 1.66, 42, 66, font-size, rem); }

.title-h4 {
  font-size: rem(25); }

.title-h5 {
  font-size: rem(20); }
