.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 85px;
  padding: 30px 15px 15px;
  min-height: 685px;
  background-color: #ddd;
  background-size: cover;
  background-position: 50% 35%;
  @include css-lock(390, 685, 767, 1200, min-height);
  @include r(1024) {
    padding: 45px 15px 10px; }
  @include r(767) {
    margin-bottom: 40px; }
  &__title {
    max-width: 630px;
    margin: 0 auto;
    text-align: center;
    text-transform: uppercase;
    font-weight: normal;
    color: #fff;
    @include r(767) {
      max-width: 340px;
      br {
        display: none; } } }
  &__main {
    display: block;
    @include r(1024) {
      margin-bottom: 15px;
      text-transform: initial; } }
  &__highlight {
    display: inline-block;
    padding: 8px 21px;
    font-weight: normal;
    background: $yellow;
    color: $black;
    line-height: 1.2;
    @include r(1024) {
      font-size: rem(32); }
    @include r(767) {
      padding: 10px 7px;
      font-size: rem(23); } } }
